

        import SendEpic from '../Send/Epics'
        import { SendMoneyAction } from '../Send/Reducers'
        import { ActionsObservable } from 'redux-observable'
        import { TypeKeys } from '../ActionTypes'
        import { of } from 'rxjs'
import { AnyAction } from 'redux';

        describe('Form validation shoudl function correctly', () => {


            it(``, async () => {
                // Given
                const store = {
                    value: {
                        account: {
                            totalAvailable: 1000,
                            totalSent: 0
                        }
                    }
                }

                const action: SendMoneyAction = {
                    type: TypeKeys.SEND_MONEY,
                    transaction: {
                        name: '',
                        email: '',
                        amount: '0'
                    }
                }

                const actionObservable = new ActionsObservable<AnyAction>(of( action ))

                // When
                const result = SendEpic(actionObservable, store as any )

                // Then
                const actions = await result.toPromise()

                console.log( actions )
            })
          })